* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: whitesmoke;
}
.header {
  display: flex;
  align-items: center;
  height: 100px;
  background: linear-gradient(90deg, #388022 1.18%, #26b3d3 100%);
  color: white;
  padding: 20px;
  width: 100%;
  margin-bottom: 20px;
}
.header--image {
  /* height: 100%;*/
  margin-right: 8px; 
  width: 45px;
  height: 38px;
}
.header--title {
  font-size: 2.1rem;
  margin-right: auto;
}
.header--project {
  font-size: 1.2rem;
  margin-right: 5%;
  font-weight: 500;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
main>.mem>img {
  margin-top: 50px;
  width: 100%;
  padding: 0 36px;
  border-radius: 30px;
  transition: filter .1s ease-in-out;
}
main>.mem>img:hover {
  filter: brightness(50%);
}
.container>button {
  width: 100%;
  border-radius: 10px;
  padding: 1rem;
  border: 2px solid lightgray;
  background-color: white;
  cursor: pointer;
  margin-top: 20px;
  font-family: 'Karla', sans-serif;
  font-size: 1.3rem;
}
main {
  width: 100%;
}
main>.fom {
  display: grid;
  gap: 17px;
  padding: 0 36px;
}
.inputs {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3%;
  /* border: 1px solid; */
  padding-left: 0px;
}
.form--input {
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
  padding-block: 20px;
  outline: none;
}
.form--button {
  grid-column: 1 / -1;
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #828d1f 1.14%, #858088 100%);
  color: white;
  border: none;
  cursor: pointer;
  padding-block: 20px;
}

.box {
  margin-block: 100px;
  display: grid;
  /* grid-template-columns: 70% 30%;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  grid-gap: 1rem; */
  /* grid-template-columns: 1fr 1fr 1fr; */
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1em;
  /* grid-auto-rows: 200px; */
  grid-auto-rows: minmax(100px, auto);
}
.nested {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 100px;
  gap: 5px;
}
.nested > div {
  border: #333 1px solid; 
}
.box > div {
  background: #eee;
}
.box > div:nth-child(odd){
  background: #ddd;
}

.thing{
  line-height: 0px;
}
input[type="text"],
input[type="email"] {
  margin-bottom: 15px;
  height: 30px;
  text-indent: 10px;
  border: 1px solid #444;
  border-radius: 5px;
  display: block;
}

input[type="checkbox"],
input[type="radio"] {
  margin-right: 0.75rem;
}

textarea {
  margin-bottom: 15px;
  border: 1px solid #444;
  border-radius: 5px;
  display: block;

}
.mem {
  position: relative;
}
.memImg {
  border-radius: 30px;
}
.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 50px;
}

@media (max-width: 430px) {

  .header{
    height: 70px;
  }
  .header--title {
    font-size: 23px;
  }

  main>.form{
    gap: 0px;
    padding: 0px;
  }
  .inputs{
    grid-template-columns: 2fr;
    gap: 20px;
  }
  .header--image{
    width: 32px;
    height: 25px;
  }
  .form--input{
    padding-block: 10px;
    margin-bottom: 0px;
  }
  input[type="text"],
  input[type="email"] {
      margin-bottom: 0px;
    }
  .form--button{
    padding-block: 12px;
  }
  main>.mem>Img{
    margin-top: 30px;
  }
  .meme--text{
    font-size: 1em;
  }
  .top{
    top: 25px;
  }
}